/* MalariaInferencePage.css */
.malaria-inference-page {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 100px); /* Adjust for the header height */
  width: 100%;
  max-width: 100vw;
  overflow: hidden; /* Ensure no overflow happens */
}

.malaria-iframe {
  flex: 1;
  width: 100%;
  height: 100%;
  border: none;
  box-sizing: border-box;
  border-radius: 10px; /* Smooth rounded corners */
}
